@import './reset.scss';
@import url('https://fonts.googleapis.com/css2?family=Passion+One&family=Poppins:wght@400;500;700&display=swap');

// font-family: 'Poppins', sans-serif;
//font-family: 'Passion One', cursive;

html,
body,
.App,
#root {
    min-height: 100vh;
}
body {
    font-family: 'Poppins', sans-serif;
    background-color: #fff;
}
.App {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
section {
    display: block;
}

.container {
    max-width: 1366px;
    margin: 0 auto;
}
.about-page__inner,
.tracking-page__inner,
.service-page__inner,
.gallery-page__inner,
.help-page__inner {
    width: 90vw;
    margin: 0 auto;
    padding: 0 5px;
    display: grid;
    grid-template-columns: 1fr 5fr;
}
.title {
    font-weight: 600;
    font-style: normal;
    color: #000;
    line-height: 1.5;
    & span {
        color: #4832dc;
    }
    &::before {
        content: '';
    }
}

.subtitle {
    font-weight: 400;
    font-style: normal;
    color: #000;
    line-height: 1.5;
}
.about-page,
.tracking-page,
.service-page,
.gallery-page,
.help-page,
.allgallery-page {
    & .header-slider {
        display: none;
    }
    & .header-tabs {
        display: none;
    }
}
.home-page{
    & .gallery__imgs-block{
        margin-bottom: 30px;
    }
}
.gallery-page{
    & .gallery__imgs-block{
        margin-bottom: 30px;
    }
}
.allgallery-page {
    & .gallery__wrapper{
        margin: 0;
        padding: 20px;
    }
    & .gallery__imgs-block{
        display: flex;
        flex-wrap: wrap;
        justify-content: space-around;
        gap: 20px;
        margin-bottom: 0;
    }
    & .gallery__img-box {
        width: 30%;
        }

    & .gallery__link {
        display: none;
    }
}


//----------screens-------------------
@media (max-width: 880px) {
    .about-page,
    .tracking-page,
    .service-page,
    .gallery-page,
    .help-page,
    .allgallery-page {
        &__inner {
            display: block;
        }
    }
     .allgallery-page {
    & .gallery__img-box {
        width: 45%;
        }
    }
}
@media (max-width: 600px) {
    body.lock {
        overflow: hidden;
    }
    .help-page{
        & .help-form{
            padding: 0;
        }
    }
}
@media (max-width: 500px){
    .allgallery-page {
    & .gallery__img-box {
        width: 100%;
        }
    }
}

